$phone: 767px;

// Tablets and small desktops
$tablet: 1024px;

$smDesktop: 1280px;

$smPhone: 320px;

$desktop: 1920px;

@mixin phone {
  @media (max-width: #{$phone}) {
    @content;
  }
}
@mixin desktop {
  @media (max-width: #{$desktop}) {
    @content;
  }
}

@mixin smDesktop {
  @media (max-width: #{$smDesktop}) {
    @content;
  }
}

@mixin smPhone {
  @media (max-width: #{$smPhone}) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: #{$tablet}) {
    @content;
  }
}
